.nav {
  @include font-title;
  letter-spacing: .1em;
  li {
    display: inline-block;
    @include notfirst {
      margin-left: 50px;
      @include xl {
        margin-left: 30px; }
      @include lg {
        margin-left: 0; } }
    @include lg {
      display: block;
      margin-bottom: 1em; } }
  a {
    @include tr(color .3s);
    @include hover {
      color: $accent; } }
  &__link {
    &--current {
      color: $accent; } }

  &--footer {
    @include font-content;
    letter-spacing: inherit;
    li {
      display: block;
      @include notfirst {
        margin-left: 0; }
      @include notlast {
        margin-bottom: em(10); } } } }
